/*
* 日程管理 store
*/
import {observable, action} from 'mobx';
import Config from '@/config';
import {requestPost} from "@/utils/api";
import journalSchemas from "@/utils/Schemas/journal";


class AgendaStore {
  @observable weekData = [];

  // 保存日程
  @action
  saveSchedules(param) {
    return requestPost('saveSchedules', Config.journal.saveSchedules, journalSchemas.journal.saveSchedules, {
      s: param
    })
  }

  // 获取详细日程
  @action
  getSchedulesById(param) {
    return requestPost('getSchedulesById', Config.journal.getSchedulesById, journalSchemas.journal.getSchedulesById, {
      id: param
    })
  }

  // 获取日程列表
  @action
  getSchedulesByWeek(param) {
    return requestPost('getSchedulesByWeek', Config.journal.getSchedulesByWeek, journalSchemas.journal.getSchedulesByWeek, {
      dateTime: param
    })
  }

  // 获取人员列表
  @action
  getUserList() {
    return requestPost('getUserList', Config.journal.getUserList, journalSchemas.journal.getUserList, {
      text: ""
    })
  }

  // 删除
  @action
  delSchedulesById(param) {
    return requestPost('delSchedulesById', Config.journal.delSchedulesById, journalSchemas.journal.delSchedulesById, {
      id: param
    })
  }

  // 根据日期获取周信息
  @action
  getWeekByTime(param) {
    return requestPost('getWeekByTime', Config.journal.getWeekByTime, journalSchemas.journal.getWeekByTime, {
      dateTime: param
    })
  }

  // 添加置顶
  @action
  saveMyTopUser(param) {
    return requestPost('saveMyTopUser', Config.journal.saveMyTopUser, journalSchemas.journal.saveMyTopUser, {
      id: param
    })
  }

  // 删除置顶
  @action
  delMyTopUser(param) {
    return requestPost('delMyTopUser', Config.journal.delMyTopUser, journalSchemas.journal.delMyTopUser, {
      id: param
    })
  }
}


export default AgendaStore;



